<template>
    <div class="hello" :key="key">
        <!-- swipe div -->
        <div class="swipe-div">
            <van-swipe ref="mySwipe"
                       @change="onSwipeChange"
                       :show-indicators="false"
                       :loop="false"
                       :touchable="false">
                <van-swipe-item>
                    <Homepage></Homepage>
                </van-swipe-item>
                <van-swipe-item>
                    <Findpage :activeIndex="active"></Findpage>
                </van-swipe-item>
                <van-swipe-item>
                    <Minepage></Minepage>
                </van-swipe-item>
            </van-swipe>
        </div>

        <!-- tabs view -->
        <van-tabbar class="tabbar-class" v-model="active"
                    @change="onTabChange"
                    active-color="#07c160"
                    inactive-color="#000" v-show="tabBarShow">
            <van-tabbar-item :dot="dot1">
                <span>首页</span>
                <img
                        slot="icon"
                        slot-scope="props"
                        :src="props.active ? homeIcon.active : homeIcon.normal"
                >
            </van-tabbar-item>
            <van-tabbar-item :dot="dot2">
                <span>发现</span>
                <img
                        slot="icon"
                        slot-scope="props"
                        :src="props.active ? findIcon.active : findIcon.normal"
                >
            </van-tabbar-item>
            <van-tabbar-item :dot="dot3">
                <span>我的</span>
                <img
                        slot="icon"
                        slot-scope="props"
                        :src="props.active ? mineIcon.active : mineIcon.normal"
                >
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    import homeImg from '@/assets/imgs/home.png';
    import Homepage from "../homepage/Homepage";
    import Findpage from "../findpage/Findpage";
    import Minepage from "../minepage/Minepage";
    import EventBus from "../../utils/eventBus";
    import log from "../../utils/log";
    import {store, storage} from "../../utils/store";
    import {getRedNotice} from "../../services/homePageService";
    import { getUserInfoService } from "../../services/mineService.js"
    import {
        updateCustNews
    } from "../../services/loginService.js";
    import {getUnreadCount} from "../../services/noticeService";

    export default {
        name: 'Home',
        components: {Minepage, Findpage, Homepage},
        data() {
            return {
                tabBarShow: true,
                debug: false,
                active: 0,
                key: 1,
                redNoticeActive: false,
                dot1: false,
                dot2: false,
                dot3: false,
                homeIcon: {
                    normal: homeImg,
                    active: require('../../assets/imgs/home_on.png')
                },
                findIcon: {
                    normal: require('../../assets/imgs/find.png'),
                    active: require('../../assets/imgs/find_on.png')
                },
                mineIcon: {
                    normal: require('../../assets/imgs/mine.png'),
                    active: require('../../assets/imgs/mine_on.png')
                },
                isRefresh: true,
                userDataUpdating: false,
            }
        },
        beforeRouteLeave(to, from, next) {
            log.l("before route leave method.");
            next();
        },
        beforeCreate() {
            log.l("before create method.");
        },
        created() {
            store.set('openNotificationForFind', (event) => {
                log.i(JSON.stringify(event));
                try {
                    let noteCode = event.extras.noteCode;
                    if (noteCode) {
                        // 跳转到发现界面
                        log.i("update new msg and switch to find page for jpush.");
                        store.get("updateNewMsgForFind")();
                        this.$refs.mySwipe.swipeTo(1, {immediate: true});
                        this.active = 1;
                    }
                } catch (e) {
                    log.i("exception for go to find page.");
                }
            });
        },/*
        beforeMount() {
            log.i("beforeMount method.");
        },
        mounted() {
            log.i("mounted method.");
        },
        beforeUpdate() {
            log.i("beforeUpdate method.");
        },
        updated() {
            log.i("updated method.");
        },
        beforeRouteUpdate() {
            log.i("beforeRouteUpdate method.");
        },
        beforeRouteEnter() {
            log.i("beforeRouteEnter method.");
        },*/
        activated() {
            this.setStore();
            /*this.isRefresh = false;
            this.isRefresh = true;*/
            this.active = 0;
            this.key = 1 - this.key;
            //this.$forceUpdate();
            this.asyncUpdateRedNote();
            this.monitorVisibileState();
            this.updateDot(2, false);
            this.getUnreadCount();
        },
        methods: {
            setStore() {
                let userInfo = storage.getStorage('custNews');
                if (!userInfo.$s_code_$exp) {
                    store.set('userInfo', userInfo);
                } else {
                    // this.$router.push('/login');
                }
                store.set('hideTabBar', (hideType) => {
                    this.tabBarShow = !hideType;
                })
                store.set('updateUnreadMsg', () => {
                    this.getUnreadCount();
                })
            },
            onSwipeChange(index) {
                // vue order on-model can double-bind tabbar with active
                this.active = index;
                // EventBus.$emit({
                //     activeIndex:index
                // })
            },
            onTabChange(index) {
                // on-model for swipe is no use, should use refs to call api swipTo
                this.$refs.mySwipe.swipeTo(index, {immediate: true});
                if (1 === index) {
                    if (true == this.dot2) {
                        this.updateDot(index, false);
                        // if need, update find data after update dot
                    }
                    this.getUnreadCount();
                } else {
                    let custNews = store.get('custNews');
                    custNews = custNews ? custNews: storage.getStorage("custNews");
                    store.get('updateHomeRedNote')(custNews.AREA_CODE, custNews.USER_CODE);
                    if (2 == index) {
                        console.log('switch to mine page.');
                        store.get('updateMineMsg')();
                        this.updateDot(2, false);
                    } else {
                        this.getUnreadCount();
                    }
                }
                store.set('currentTabIndex', index);
            },
            updateDot(index, value) {
                switch (index) {
                    case 0:
                        this.dot1 = value;
                        break;
                    case 1:
                        this.dot2 = value;
                        break;
                    case 2:
                        this.dot3 = value;
                        break;
                }
            },
            asyncUpdateRedNote() {
                store.set("updateHomeRedNote", (areaCode, userCode) => {
                    this.getRedNotice(areaCode, userCode);
                });
            },
            getRedNotice: function (areaCode, userCode) {
                var query = {
                    AREA_CODE: areaCode,
                    USER_CODE: userCode
                };
                query.AREA_CODE = areaCode;
                query.USER_CODE = userCode;
                getRedNotice(query).then(
                    (answer) => {
                        if (answer.READFLAG > 0) {
                            this.redNoticeActive = true;
                            this.updateDot(1, true);
                        } else {
                            this.redNoticeActive = false;
                            if (this.debug) {
                                this.redNoticeActive = true;
                                this.updateDot(1, true)
                            } else {
                                this.updateDot(1, false);
                            }
                        }
                    }
                );
            },
            showMsg: function (msg) {
                this.$toast(msg);
            },
            monitorVisibileState: function () {
                document.addEventListener("visibilitychange", () => {
                    if (document.visibilityState == "visible") {
                        // log.i("home comp visible now.");
                    } else if (document.visibilityState == "hidden") {
                        // update user data and update into local storage and store
                        this.updateUserInfo();
                    }
                });
            },
            updateUserInfo: function() {
                if (this.userDataUpdating) {
                    log.i("user data updating when reuupdate user info");
                    return;
                }
                this.userDataUpdating = true;
                log.i("update user info");
                let custNews = store.get('custNews');
                custNews = custNews ? custNews: storage.getStorage("custNews");
                let userData = {
                    AREA_CODE: custNews.AREA_CODE,
                    USER_CODE: custNews.USER_CODE,
                };
                getUserInfoService(userData)
                    .then(noteData => {
                        let userPic = noteData.PIC_URL;
                        if (userPic != '') {
                            custNews.PIC_URL = userPic;
                        }
                        custNews.USER_ACCNT = noteData.USER_ACCNT;
                        custNews.USER_NICK_NAME = noteData.USER_NICK_NAME;
                        custNews.PHONE = noteData.PHONE;
                        updateCustNews(custNews);
                        this.userDataUpdating = false;
                    }, err => {
                        this.userDataUpdating = false;
                    })
                    .catch(error => {
                        this.userDataUpdating = false;
                        log.e(error);
                    });
            },
            getUnreadCount() {
                let userInfo = store.get("userInfo") ? store.get("userInfo") : storage.getStorage("custNews");
                getUnreadCount({userCode: userInfo.USER_CODE})
                .then(result => {
                    if (result.resultCode == 0) {
                      let msgTip = result.data.unReadMsgCount > 0 ? true : false;
                      this.updateDot(2, msgTip);
                      console.log("home get unread msg count success " + this.msgTip);
                    } else {
                      console.log("home get unread msg count failed.");
                    }
                })
                .catch(err => {
                    console.log("home get unread msg count failed for " + err);
                });
            }
        }
    }
</script>

