  getSystemDicByParent,
  getSystemDicChildren
} from "xt/services/register";
export default {
  name: "Homepage",
  components: { AppTitle },
  data() {
    return {
      showXtMethod: false,
      //管控平台小区列表
      xtAreaList: [],
