      isOperation: 0,
      orderNum: { auditReturnOrderNum: 0, recOrderNum: 0, refundOrderNum: 0 },
      active: false,
      imgWorkPlan: require("../../assets/imgs/home/icon_ownerWorkPlan.png"),
      imgWrokCheck: require("../../assets/imgs/home/icon_ownerWorkCheck.png"),
      imgInsideWorkPlan: require("../../assets/imgs/home/icon_insideWorkPlan.png"),
      imgInsideWorkCheck: require("../../assets/imgs/home/icon_insideWorkCheck.png"),
      imgHouseCheck: require("../../assets/imgs/home/houseCheck.png"),
      imgActivity: require("../../assets/imgs/home/acivity.png"),
      imgSale: require("../../assets/imgs/commerce/home_icon_community_sales.png"),
      icon_work: require("../../assets/imgs/home/icon_small_work.png"),
      icon_inside_work: require("../../assets/imgs/home/icon_small_insideWork.png"),
      icon_delay: require("../../assets/imgs/home/icon_small_delay.png"),
      icon_inside_delay: require("../../assets/imgs/home/icon_small_insideDelay.png"),
      imgMore: require("../../assets/imgs/home/left.png"),
      iconOrderList: require("../../assets/imgs/commerce/icon_order_list.png"),
      purchaseOrderList: require("../../assets/imgs/commerce/icon_purchaseorder_list.png"),
      returnOrderList: require("../../assets/imgs/commerce/icon_returnorder_list.png"),
      saleSrcImg: require("../../assets/imgs/commerce/bg_community sales.png"),
      imgVisitor: require("../../assets/imgs/home/visitor.png"),
      imgRecognition: require("../../assets/imgs/home/recognition.png"),
      custNews: {},
      homeBackStyle: {
        backgroundImage:
          "url(" + require("../../assets/imgs/home/headerBac.png") + ")",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat"
      }
    };
  },
  watch: {
    $route: function(to, from) {
      let currentTab = store.get("currentTabIndex", -1);
      if ((currentTab <= 0 || !currentTab) && "home" == to.name) {
        // from other to home
        log.i("route to " + to.name + " and curr homepage " + currentTab);
        this.villageList();
        // update msg unread red point for tab mine
        store.get("updateUnreadMsg")();
      }
      if (currentTab == 1 && "home" == to.name && "newMsg" == from.name) {
        // from new msg list to home tab find
        log.i(
          "route from " +
            from.name +
            " to " +
            to.name +
            " and curr find " +
            currentTab
        );
        store.get("updateNewMsgForFind")();
      }
      if (currentTab == 2 && "home" == to.name && "SystemNotice" == from.name) {
        // from msg center to home tab mine
        log.i(
          "route from " +
            from.name +
            " to " +
            to.name +
            ", curr mine " +
            currentTab
        );
        store.get("updateMineMsg")();
      }
    }
  },
  methods: {
    onRefresh() {
      this.villageList();
    },

    syncSelectionInfo(select) {
      // todo: update data by selection info
      this.currentSelection = select;
      this.changeArea(this.villList[select].AREA_CODE);
    },
    showWorkPlanDetail: function() {
      store.set("workDetailComplCode", this.workData.complCode);
      this.$router.push({
        name: "workDetail"
      });
    },
    showInsideWorkPlanDetail: function() {
      store.set("insideWorkDetailComplCode", this.workDataInside.complCode);
      this.$router.push({
        name: "workDetailInside"
      });
    },
    goToWorkPlanDetail: function(complCode, type, isReceiverNow) {
      let notReceiverNow = isReceiverNow == "false" ? true : false;
      if (type == 0) {
        // 业主工单
        store.set("workDetailComplCode", complCode);
        this.$router.push({
          name: "workDetail",
          params: { isNotReceiverNow: notReceiverNow }
        });
      } else if (type == 1) {
        // 内部工单
        store.set("insideWorkDetailComplCode", complCode);
        this.$router.push({
          name: "workDetailInside",
          params: { isNotReceiverNow: notReceiverNow }
        });
      } else {
        // do not care
        console.log("no supporeted work plan type " + type);
      }
    },
    showWorkPlanList: function() {
      // go to work plan list
      store.set("reloadWorkListStatus", 1);
      this.$router.push("workPlan");
    },
    showInsideWorkPlanList: function() {
      // go to work plan list
      store.set("reloadInsideWorkListStatus", 1);
      this.$router.push("workPlanInside");
    },
    showWorkDelayDetail: function() {
      // go to work delay detail with info
      this.goWorkDelayDetail(this.workDelayData);
    },
    showInsideWorkDelayDetail: function() {
      this.goInsideWorkDelayDetail(this.workDelayDataInside);
    },
    goToWorkDelayDetail: function(complCode, type, uuid) {
      if (type == 0) {
        // 业主延期
        this.$router.push({
          name: "delayDetail",
          params: {
            complCode: complCode,
            uuid: uuid
          }
        });
      } else if (type == 1) {
        // 内部延期
        this.$router.push({
          name: "delayDetailInside",
          params: {
            complCode: complCode,
            uuid: uuid
          }
        });
      } else {
        // do not care.
      }
    },
    showWorkDelayList: function(tab) {
      // todo: go to work delay list
      // this.showMsg("work delay list show.");
      store.set("workDelayTab", tab);
      this.$router.push("workDelay");
    },
    showInsideWorkDelayList: function(tab) {
      store.set("insideWorkDelayTab", tab);
      this.$router.push("workDelayInside");
    },
    showHouseCheckList: function() {
      // todo: go to house check list
      // this.showMsg("house check list show.");
      this.$router.push("checkHouse");
    },
    goToActivityList: function() {
      // go to activity list
      // this.$router.push("activityManage");
      this.$router.push({'name':'xtHome'});
    },
    goToSaleEntrace: function() {
      if (this.custNews.AREA_CODE) {
        log.i("HomePage: area code info " + this.custNews.AREA_CODE);
        //this.$router.push("commerceEntrace");
        this.$router.push({
          name: "commerceEntrace",
          params: {
            userInfo: this.custNews
          }
        });
      }
    },
    showMsg: function(msg) {
      this.$toast(msg);
    },
    debugClick: function() {
      const debug = this.debug;
      if (debug) {
        this.debugClkNum++;
        if (this.debugClkNum >= 20) {
          this.debugClkNum = 0;
          this.$dialog
            .confirm({
              title: "调试退出",
              message: "是否退出？"
            })
            .then(() => {
              updateCustNews();
              storage.deleteStorage("custNews");
              this.$router.replace({ path: "login" });
            })
            .catch(() => {
              // do not care.
            });
        }
      }
    },
    villageList: function() {
      // for refresh logic
      if ("" === this.custNews.USER_CODE) {
        log.w("the custNews is " + JSON.stringify(this.custNews));
      }
      var data = {
        USER_CODE: this.custNews.USER_CODE
      };

      getVillageList({ data: JSON.stringify(data) }).then(
        res => {
          this.villList = res;
          this.$refs.appTitle && this.$refs.appTitle.syncVillList(res);
          log.l("the village list request result " + JSON.stringify(res));
          let title = this.$refs.appTitle;
          //选择一次后记住选中小区
          if ("" == this.custNews.AREA_CODE) {
            if (undefined !== this.villList && this.villList.length > 0) {
              this.dataArea = this.villList[0].AREA_CODE;
              this.custNews.AREA_CODE = this.villList[0].AREA_CODE;
              this.custNews.AREA_NAME = this.villList[0].AREA_NAME;
              //log.i("HomePage-1 " + JSON.stringify(this.custNews));
              updateCustNews(this.custNews);
              this.getXtSystemAreaList(this.custNews);
              log.i("HomePage " + JSON.stringify(this.villList[0]));
              storage.setStorage("custNews", this.custNews, 60);
            } else {
              log.l("the village list size " + this.villList.length);
            }
          } else {
            for (var i = 0; i < this.villList.length; i++) {
              if (this.custNews.AREA_CODE == this.villList[i].AREA_CODE) {
                this.custNews.AREA_CODE = this.villList[i].AREA_CODE;
                this.custNews.AREA_NAME = this.villList[i].AREA_NAME;
                //log.i("HomePage-2 " + JSON.stringify(this.custNews));
                updateCustNews(this.custNews);
                this.getXtSystemAreaList(this.custNews);
                storage.setStorage("custNews", this.custNews, 60);
                if (title) {
                  title.updateSelection(i);
                }
                break;
              }
            }
            this.dataArea = this.custNews.AREA_CODE;
          }
          this.areaName = this.custNews.AREA_NAME;
          this.getRedNotice(this.custNews.AREA_CODE); //查询小红点
          if (
            this.isOperation == 0 ||
            (this.isOperation == undefined && this.isSale == 0)
          ) {
            //物业销售
            //加载小区订单数据
            /*this.getOrderNum();
          eventBus.$off("reloadSaleNum")
          eventBus.$on('reloadSaleNum', target => {
            this.getOrderNum();
          });
        } else { */
            //非物业销售
            this.getPostCode(this.custNews.AREA_CODE);
            // this.getRedNotice(this.custNews.AREA_CODE);//查询小红点
            store.get("updateHomeRedNote")(
              this.custNews.AREA_CODE,
              this.custNews.USER_CODE
            );
            this.workUndoList();
            this.workDelayList();
            this.insideWorkUndoList();
            this.insideWorkDelayList();
          }
          //设置标签
          var tags = [];
          for (var j = 0; j < this.villList.length; j++) {
            try {
              this.villList.forEach(item => {
                // for jPush method.
                tags.push(item.AREA_CODE);
              });
            } catch (e) {
              log.e(e);
            }
          }

          // this.setTags(tags);
          setTags(this.custNews, tags);

          // update loading state
          // this.$toast('刷新成功');
          this.isLoading = false;

          /*判断首页是否已经选中*/
        },
        error => {
          this.isLoading = false;
        }
      );
    },
    getOrderNum: function() {
      if (!this.custNews.AREA_CODE) {
        log.i("area code empty, do not request order number.");
        return;
      }
      var data = {
        recAreaCode: this.custNews.AREA_CODE
      };
      getOrderNum(data)
        .then(
          res => {
            log.l("the order num request result " + res);
            if (res.resultCode == "0") {
              this.orderNum = res.data;
              log.i(this.orderNum);
            } else {
              res.msg
                ? this.showMsg(res.msg)
                : this.showMsg("网络异常，请稍后重试！");
            }
          },
          error => {
            log.e("get order number " + JSON.stringify(error));
          }
        )
        .catch(error => {
          log.e("get order exception " + JSON.stringify(error));
        });
    },
    getPostCode: function(areaCode) {
      var data = {
        AREA_CODE: areaCode,
        USER_CODE: this.custNews.USER_CODE
      };
      getPostCode(data)
        .then(
          res => {
            log.l("the post code request result " + res);
            if (undefined !== res && null !== res) {
              this.custNews.POST_CODE = res.POST_CODE;
              //log.i("HomePage-3 " + JSON.stringify(this.custNews));
              updateCustNews(this.custNews);
              storage.setStorage("custNews", this.custNews, 60);
            }
          },
          error => {
            this.showMsg("获取岗位代码错误" + error);
          }
        )
        .catch(error => {
          this.showMsg("获取岗位代码异常" + error);
        });
    },
    getRedNotice: function(areaCode) {
      // todo: update tab red point
      var query = {
        AREA_CODE: this.custNews.AREA_CODE,
        USER_CODE: this.custNews.USER_CODE
      };
      query.AREA_CODE = areaCode;
      getRedNotice(query).then(answer => {
        log.l("the notice red request result " + JSON.stringify(answer));
        if (answer.READFLAG > 0) {
          this.active = true;
        } else {
          this.active = false;
        }
      });
    },
    workUndoList: function() {
      var data = {
        userCode: this.custNews.USER_CODE,
        statusList: 0,
        page: 1, //分页页号
        rows: 1
      };
      getWorkingPlanList(data)
        .then(
          result => {
            log.l("the wrok undo list " + JSON.stringify(result));
            if (
              result.resultCode == "0" &&
              result.data.list &&
              result.data.list.length > 0
            ) {
              this.workData = result.data.list[0];
            } else {
              this.workData = {};
            }
          },
          error => {
            log.e("work undo list fetch failed " + error);
            this.workData = {};
          }
        )
        .catch(error => {
          log.e("work undo list fetch exception " + error);
          this.workData = {};
        });
    },
    insideWorkUndoList: function() {
      var data = {
        userCode: this.custNews.USER_CODE,
        statusList: 0,
        page: 1, //分页页号
        rows: 1
      };
      getInterWorkingPlanList(data)
        .then(
          result => {
            log.l("the internal wrok undo list " + JSON.stringify(result));
            if (
              result.resultCode == "0" &&
              result.data.list &&
              result.data.list.length > 0
            ) {
              this.workDataInside = result.data.list[0];
            } else {
              this.workDataInside = {};
            }
          },
          error => {
            log.e("inter work undo list fetch failed " + error);
            this.workDataInside = {};
          }
        )
        .catch(error => {
          log.e("inter work undo list fetch exception " + error);
          this.workDataInside = {};
        });
    },
    insideWorkDelayList: function() {
      var data = {
        userCode: this.custNews.USER_CODE,
        reportPhone: this.custNews.PHONE,
        approvalType: "", // 不传类型，默认获取全部的
        approvalStatus: "0", // 1是未审批，2为已审批，只请求未审批的
        page: 1,
        rows: 10,
        areaCode: this.custNews.AREA_CODE,
        builCode: ""
      };

      getInterDelayList(data).then(
        res => {
          if (
            res.resultCode == 0 &&
            res.data.list &&
            res.data.list.length > 0
          ) {
            this.workDelayDataInside = res.data.list[0];
            this.getInterWorkPlanDetail(res.data.list[0].complCode);
            this.getMyDelayInfoInside(
              res.data.list[0].complCode,
              res.data.list[0].uuid
            );
          } else {
            this.workDelayDataInside = {};
          }
        },
        error => {
          this.workDelayDataInside = {};
        }
      );
    },
    workDelayList: function() {
      var data = {
        userCode: this.custNews.USER_CODE,
        reportPhone: this.custNews.PHONE,
        approvalType: "", // 不传类型，默认获取全部的
        approvalStatus: "0", // 1是未审批，2为已审批，只请求未审批的
        page: 1,
        rows: 10,
        areaCode: this.custNews.AREA_CODE,
        builCode: ""
      };

      getDelayList(data).then(
        res => {
          if (
            res.resultCode == 0 &&
            res.data.list &&
            res.data.list.length > 0
          ) {
            this.workDelayData = res.data.list[0];
            this.getWorkPlanDetail(res.data.list[0].complCode);
            this.getMyDelayInfo(
              res.data.list[0].complCode,
              res.data.list[0].uuid
            );
          } else {
            this.workDelayData = {};
          }
        },
        error => {
          this.workDelayData = {};
        }
      );
    },
    // 获取工单内容
    getWorkPlanDetail: function(complCode) {
      var workDetaildata = {
        complCode: complCode,
        userCode: this.custNews.USER_CODE
      };
      getWorkPlanDetail(workDetaildata).then(
        res => {
          if (res.resultCode == "0") {
            this.workDetail = res.data;
          }
        },
        err => {}
      );
    },
    getInterWorkPlanDetail: function(complCode) {
      var workDetaildata = {
        complCode: complCode,
        userCode: this.custNews.USER_CODE
      };
      getInterWorkPlanDetail(workDetaildata).then(
        res => {
          if (res.resultCode == "0") {
            this.workDetailInside = res.data;
          }
        },
        err => {}
      );
    },

    // 获取审批内容
    getMyDelayInfo: function(complCode, uuid) {
      var data = {
        uuid: uuid
      };
      getDelayDetail(data).then(
        res => {
          if (res.resultCode == "0") {
            this.approvalDetail = res.data;
          }
        },
        err => {}
      );
    },
    getMyDelayInfoInside: function(complCode, uuid) {
      var data = {
        uuid: uuid
      };
      getDelayDetail(data).then(
        res => {
          if (res.resultCode == "0") {
            this.approvalDetailInside = res.data;
          }
        },
        err => {}
      );
    },
    setTags: function(tags) {
      //设置极光推送 标签
      log.l(JSON.stringify(tags));
      // set tags for jPushPlugin

      var obj = {
        sequence: Number(this.custNews.USER_CODE),
        tags: tags
      };
      if (!!window.JPush) {
        log.i("heijianye- set tags with JPush.");
        window.JPush.setTags(
          obj,
          function(result) {
            log.l("JPushPlugin:setTags is success" + result);
          },
          function(error) {
            log.l("JPushPlugin:setTags is error" + error);
          }
        );
      } else {
        window.plugins.jPushPlugin.setTags(
          obj,
          function(result) {
            log.l("JPushPlugin:setTags is success" + result);
          },
          function(error) {
            log.l("JPushPlugin:setTags is error" + error);
          }
        );
      }
    },
    changeArea: function(area) {
      let userInfo = store.get("custNews");
      let userInfoInS = storage.getStorage("custNews");
      // update custNews first if user info changed by mine page.
      if (userInfo && userInfo.USER_NICK_NAME != this.custNews.USER_NICK_NAME) {
        this.custNews = userInfo;
      } else if (
        userInfoInS &&
        userInfoInS.USER_NICK_NAME != this.custNews.USER_NICK_NAME
      ) {
        this.custNews = userInfoInS;
      }
      this.custNews.AREA_CODE = area;
      //log.i("HomePage-4 " + JSON.stringify(this.custNews));
      updateCustNews(this.custNews);
      storage.setStorage("custNews", this.custNews, 60);
      // notify find to update.
      store.get("listenToAreCodeChange")();
      for (var i = 0; i < this.villList.length; i++) {
        if (area == this.villList[i].AREA_CODE) {
          this.custNews.AREA_CODE = area;
          this.custNews.AREA_NAME = this.villList[i].AREA_NAME;
          storage.setStorage("custNews", this.custNews, 60);
          this.areaName = this.custNews.AREA_NAME;
          this.getRedNotice(this.custNews.AREA_CODE); //查询小红点
          this.getXtSystemAreaList(this.custNews);
          if (
            this.isOperation == 0 ||
            (this.isOperation == undefined && this.isSale == 0)
          ) {
            //物业销售
            //加载小区订单数据
            /*this.getOrderNum();
          } else {*/
            //非物业销售
            this.getPostCode(this.custNews.AREA_CODE);
            this.workUndoList();
            this.workDelayList();
          }
        }
      }
    },
    goWorkDelayDetail: function(item) {
      this.$router.push({
        name: "delayDetail",
        params: {
          complCode: item.complCode,
          uuid: item.uuid
        }
      });
    },
    goInsideWorkDelayDetail: function(item) {
      this.$router.push({
        name: "delayDetailInside",
        params: {
          complCode: item.complCode,
          uuid: item.uuid
        }
      });
    },
    gotoOrderList: function(status) {
      // 跳转订单列表
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }

      // go to commerceOrderList
      this.$router.push({
        name: "commerceOrderList",
        params: {
          orderInfo: status
        }
      });
    },
    gotoReturnList: function(status) {
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }
      // goto commerceReturnList
      // 跳转退货列表
      this.$router.push({
        name: "commerceReturnList",
        params: {
          orderInfo: status
        }
      });
    },
    gotoPurchaseList: function(status) {
      if (
        this.custNews.AREA_CODE == "" ||
        this.custNews.AREA_CODE == undefined
      ) {
        this.showMsg("账号权限未分配，请联系管理员。");
        return;
      }
      // goto commercePurchaseList
      // 跳转采购列表
      this.$router.push({
        name: "commercePurchaseList",
        params: {
          load: true
        }
      });
    },
    //获取管控平台小区列表
    getXtSystemAreaList(area) {
      console.log("获取管控平台小区列表", area);
      this.showXtMethod = false;
      this.homeBackStyle.height = "288px";
      if (this.xtAreaList.length) {
       area &&  this.selectXtSystemArea(area);
      } else {
        getSystemDicByParent().then(res => {
          console.log("获取系统权限成功", res);
          if (res.code === 200) {
            this.xtAreaList = res.data.list;
            area && this.selectXtSystemArea(area);
          }
        });
      }
    },
    //选择xt系统小区
    selectXtSystemArea(area) {
      this.xtAreaList.forEach(element => {
        console.log("element", JSON.parse(element.remark), area.AREA_CODE);
        const newE = JSON.parse(element.remark);
        if (newE.AREA_CODE === area.AREA_CODE) {
          console.log("xt",JSON.stringify(newE));
          console.log("tianyue",JSON.stringify(area));
          storage.setStorage("xtBaseUrl", newE.BASE_URL, 999);
          getSystemDicChildren(element.dicCode)
            .then(res => {
              if (res.code === 200) {
                res.data.list.forEach(element => {
                  let child = JSON.parse(element.data.remark);
                  if (child.APP_CODE === "2000") {
                    this.homeBackStyle.height = "288px";
                    this.showXtMethod = true;
                  }
                });
              }
            })
            .catch(err => {
              console.log("获取三通应用失败", err);
            });
        }
      });
    },
    goVisitorRegister: function() {
      // ****** 三通登陆拦截 ******
      // debugger
      let data = {
        roleId: 999,
        userCode: this.custNews.USER_CODE,
        // regionId: this.custNews.AREA_CODE,
        userAccnt: this.custNews.USER_ACCNT,
        phone: this.custNews.PHONE,
        userName: this.custNews.USER_NAME,
        userNickName: this.custNews.USER_NICK_NAME
      };
      let controlToken = controlToken
        ? controlToken
        : storage.getStorage("controlToken");
      if (controlToken.$s_code_$exp) {
        // 不存在
        getControlTokenService(data)
          .then(res => {
            console.log("访客登录成功", res);
            console.log("112", res);
            if (res.code === 200) {
              //成功
              const { token } = res.data;
              storage.setStorage("controlToken", token, 999);
              this.$router.push({ path: "/visitorRegister/register" });
            } else {
              res.message && this.$toast(res.message);
            }
          })
          .catch(err => {
            console.log("113", err);
            this.$toast("当前网络不可用");
          });
      } else {
        this.$router.push({ path: "/visitorRegister/register" });
      }
    },
    goRecognition: function() {
      this.$router.push({ path: "/recognition" });
    }
  },
  created() {
    // get isSale and village list when init
    // this.custNews = storage.getStorage('custNews');
    this.getXtSystemAreaList(null);
    this.custNews = store.get("userInfo")
      ? store.get("userInfo")
      : storage.getStorage("custNews");
    if (undefined !== this.custNews) {
      // log.i(JSON.stringify(this.custNews));
      this.isSale = this.custNews.sales_man_flag;
      this.isOperation = this.custNews.operation_flag;
      log.i(
        "The sles man flage is " +
          this.custNews.sales_man_flag +
          "; oper flag " +
          this.isOperation
      );
      this.villageList();
    } else {
      log.e("The user info empty, can not fetch village list!!!");
    }
    store.set("listenToNotificationOpen", event => {
      // 跳转到工单详情/延期审批详情（内部/业主）
      log.i(JSON.stringify(event));
      try {
        let complCode = event.extras.complCode;
        let type = event.extras.operationNum;
        let resource = event.extras.complResource;
        let uuid = event.extras.uuid;
        if (type == "7" || type == "8") {
          // 延期申请 延期申请指派
          this.goToWorkDelayDetail(complCode, resource, uuid);
        } else if (
          type == "1" ||
          type == "2" ||
          type == "4" ||
          type == "5" ||
          type == "6" ||
          type == "10" ||
          type == "11"
        ) {
          // 工单创建 指派 催单 完成 评价 非正常关闭 超时未接单
          let isReceiverNow = true;
          if ("1" == type || "2" == type || "11" == type) {
            // 待接单的情况需要判断是否当前处理人，如果已经不是当前处理人了，则展示详情时提示工单已被接取
            // To check if is current dealer for the work plan clicked through jPush;
            // if not, should show the work plan has been received by others in work detail page.
            var data = {
              userCode: this.custNews.USER_CODE,
              complCode: complCode
            };
            checkIfReceiver(data)
              .then(result => {
                console.log(JSON.stringify(result));
                if (result.resultCode == "0") {
                  isReceiverNow = result.data;
                }
              })
              .catch(error => {
                console.log("check if receiver failed " + error);
              })
              .finally(() => {
                this.goToWorkPlanDetail(complCode, resource, isReceiverNow);
              });
          } else {
            this.goToWorkPlanDetail(complCode, resource, true);
          }
        } else {
          // 9 延期申请撤销 3 撤销工单
          // else do not care
        }
      } catch (e) {
        log.i("exception for switch.");
      }
    });
    store.set("logoutListener", () => {
      log.i("HomePage, logout happend.");
      this.custNews = {};
    });
  },
  beforeDestroy() {
    // eventBus.$off("reloadSaleNum")
  }
};
</script>

