//jsrsasign获取签名
Vue.prototype.$getRSASign = function(password, key) {
  let sign = new JSEncrypt({
    default_key_size: 1024
  });
  key = key.replace("/r", "");
  key = "-----BEGIN PRIVATE KEY-----" + key + "-----END PRIVATE KEY-----";
  sign.setPublicKey(key);
  let data = sign.encrypt(password.toString());
  return data;
};
