<template>
  <div id="app">
    <div class="loading-view" v-show="loadingViewShow">
      <van-loading color="#00d8d1" />
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <div class="second-router" v-show="secondRouterShow">
      <keep-alive>
        <router-view name="secondary" v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive" name="secondary"></router-view>
    </div>
  </div>
</template>

<script>
import { store, storage } from "./utils/store";
import config from "./common/config";
import EventBus from "./utils/eventBus";
import log from "./utils/log";
import {
  deleteAlias,
  cleanTags
} from "./services/loginService.js";
import {
  getExitAppService
} from "./services/mineService";
export default {
  name: "app",
  components: {
    //合并代码注释
  },
  data() {
    return {
      isRefresh:false,
      secondRouterShow: false,
      loadingViewShow: false
    };
  },
  mounted() {
    window.logOut = this.exitLogin;
    store.set("goLoginRouter", () => {
      this.exitLogin();
    });
  },
  created() {
    //设置加载view显示回调
    store.set('showLoadingView',(isShow)=>{
      this.loadingViewShow = isShow;
    });
    //设置极光推送
    this.$plugin.usePlugin('jPush')((jPushInstace)=>{
      log.i('use jpush interface ' + jPushInstace);
    })
    let custEnv = "";
    let currentRouterName = store.get("currentRouterName");
    this.secondRouterShow = currentRouterName;
    EventBus.$on("secondRouter", showing => {
      this.secondRouterShow = showing;
    });
    //没有值
    if (storage.getStorage("custUrl").$s_code_$exp) {
      custEnv = config.urlMap[config.defaultEnv];
    } else {
      custEnv = storage.getStorage("custUrl");
    }
    store.set("custUrl", custEnv);
  },
  methods:{
    /**
     * 退出登录请求方法
     * callBack  方法回调
     **/
    exitLogin: function(callBack) {
      let custNews = store.get('custNews');
      custNews = custNews ? custNews: storage.getStorage("custNews");
      //移除推送
      deleteAlias(custNews).then((result) => {
        // delete alias success
        log.l("delete alias success " + result);
        cleanTags(custNews);
      }, (err) => {
        log.l('delete alias failed for ' + err);
      });
      //cleanTags(custNews);
      let data = {
        AREA_CODE: custNews.AREA_CODE,
        USER_CODE: custNews.USER_CODE
      };
      getExitAppService(data)
              .then(noteData => {
                if (callBack
                        && typeof callBack != 'undefined'
                        && callBack != undefined) {
                  callBack();
                }
                // 清空缓存
                store.dele('custNews');
                store.dele('userInfo');
                storage.deleteStorage("custNews");
                store.set('currentTabIndex', 0);
                // 移除推送相关
                // 清除小红点
                // 跳转到登录页
                // 通知Home清空缓存数据
                store.get("logoutListener")();
                // delete token info in store and storage
                storage.deleteStorage("accessToken");
                storage.deleteStorage("refreshToken");
                store.dele('accessToken');
                store.dele('refreshToken');
                this.$router.replace({ name: "login" });
              })
              .catch(error => {
                Toast.fail('退出失败，请检查网络');
              });
    },
  },
};
</script>

