import http from "../http";

// 获取用户信息
const getUserInfoService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/property/myNavigationController/getUserInfo.do', { data: JSON.stringify(reqData) }, true, 'homeUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}
