import http from "../http";

//获取发现全部信息服务
const getAllNoteService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-discovery-forum/note/getAppNoteList', reqData, 'homeUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        });
    });
}

//发现点赞服务
const saveLikeService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/thumpUp/thumpUp', reqData).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}

//单个发现信息更新
const getOneNoteMsgService = (replyData) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-discovery-forum/note/getNoteDetail', replyData).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(`获取发现信息失败`);
        }).catch(error => {
            reject(error);
        });
    });
}

//获取全部的数据分类列表
const getSelectionService = () => {
    return new Promise((resolve, reject) => {
        http.get('xwj-discovery-forum/noteCat/getCateList', {}).then(list => {
            resolve(list);
        }).catch(error => {
            reject(error);
        });
    });
}


//更新当前数据分类
const updateNoteCatService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/noteCat/deliverNote', reqData, true).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}

//删除指定数据
const delNoteDataService = (noteCode, userCode) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/note/deleteNote', { 'noteCode': noteCode, "userCode": userCode }, true).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}

//新加发现动态
const addNoteService = (content, userCode, areaCode, picUrl) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/note/sendNote', {
            "areaCodeList": [areaCode],
            "content": content,
            "isProperty": 1,
            "picUrl": picUrl,
            "userCode": userCode
        }).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}


