<template>
  <div class="minepage">
    <div class="mineHeader" :style="{'background-image' : 'url('+ topPic + ')'}">
      <div class="mineHeadTitle">我的</div>
      <img class="minUserImg" :src="userIcon" />
      <p>{{userName}}</p>
    </div>
    <div class="mineContent">
      <van-cell
        class="mineContentCell"
        :key="item.id"
        v-for="item in pageData"
        :icon="item.leftIcon"
        :title="item.title"
        :value="item.value"
        @click="cellClicked(item.id)"
        is-link
      >
        <div slot="right-icon" class="right-icon-div">
          <div class="tip" v-if="msgTip && item.id == 0"></div>
          <van-icon :name="item.rightIcon" class="right-vant-icon"></van-icon>
        </div>
      </van-cell>
    </div>

    <!-- 退出app弹出框 -->
    <van-dialog v-model="isShowExit" message="是否确认退出？" show-cancel-button :beforeClose="beforeClose"></van-dialog>

    <!-- 环境切换选择框 -->
    <van-action-sheet
      v-model="isShowenvSwitch"
      :round="true"
      :safe-area-inset-bottom="true"
      :actions="envSwitchActions"
      cancel-text="取消"
      @select="onSelect"
      @cancel="onCancel"
    />
  </div>
</template>

<script>
import {
  getUserInfoService,
  getExitAppService
  // getMinePageNoticeService
} from "../../services/mineService";
import {getUnreadCount} from "../../services/noticeService";
import { store, storage } from "../../utils/store";
import config from "../../common/config";

import Vue from "vue";
import { Icon, Cell, CellGroup, Toast, Dialog, ActionSheet } from "vant";
Vue.use(Cell)
  .use(CellGroup)
  .use(Icon)
  .use(Toast)
  .use(Dialog)
  .use(ActionSheet);

import { deleteAlias, cleanTags } from "../../services/loginService";
import log from '../../utils/log'

// 头部背景图片
import topPic from "@/assets/imgs/mine/mine_mine.png";
import userIcon from "@/assets/imgs/mine/mine_duck.png";
// 工单icon
import workIcon from "@/assets/imgs/msg/msg@2x.png";
// 账号信息icon
import accountIcon from "@/assets/imgs/mine/mine_account.png";
// 关于icon
import aboutIcon from "@/assets/imgs/mine/mine_info.png";
// app版本icon
import versionIcon from "@/assets/imgs/mine/mine_version.png";
// 退出icon
import exitIcon from "@/assets/imgs/mine/mine_exit.png";
// 箭头icon
import arrowIcon from "@/assets/imgs/mine/mine_arrow.png";
export default {
  name: "Minepage",
  created() {
    // 加载页面数据
    this.loadPageData();
    this.monitorVisibileState();
    store.set('updatePageData',(pagData)=>{
        this.updatePageData(pagData);
    });
    store.set('updateMineMsg', () => {
        this.getUnreadCount();
    })
  },
  data() {
    return {
      active: 0,
      // 图片和icon引入
      topPic: topPic,
      userIcon: userIcon,

      userName: "",
      // 是否显示退出框
      isShowExit: false,
      versonClickCount: 0,
      // 环境切换选择框
      isShowenvSwitch: false,
      envSelectIndex: "666",
      envSwitchActions: [],

      // 页面数据
      pageData: [
        // {
        //   id: 0,
        //   title: "我的工单",
        //   leftIcon: workIcon,
        //   rightIcon: arrowIcon,
        //   value: ""
        // },
        {
          id: 0,
          title: "消息中心",
          leftIcon: workIcon,
          rightIcon: arrowIcon,
          value: ""
        },
        {
          id: 1,
          title: "账户信息",
          leftIcon: accountIcon,
          rightIcon: arrowIcon,
          value: ""
        },
        {
          id: 2,
          title: "关于我们",
          leftIcon: aboutIcon,
          rightIcon: arrowIcon,
          value: ""
        },
        // {
        //   id: 3,
        //   title: "app版本号",
        //   leftIcon: versionIcon,
        //   rightIcon: "",
        //   value: config.version
        // },
        {
          id: 4,
          title: "退出",
          leftIcon: exitIcon,
          rightIcon: "",
          value: ""
        }
      ],
      msgTip: false,
    };
  },
  watch: {
    isShowenvSwitch(showing) {
      store.get("hideTabBar")(showing);
    }
  },
  methods: {
    monitorVisibileState: function () {
      document.addEventListener("visibilitychange", () => {
        if (document.visibilityState == "visible") {
          // visible update pic
          let userInfo = store.get("userInfo") ? store.get("userInfo") : storage.getStorage("custNews");
          this.updatePageData(userInfo);
        } else if (document.visibilityState == "hidden") {
          // do not care
        }
      });
    },
    // 更新页面数据
    updatePageData(pagData) {
      let userPic = pagData.PIC_URL;
          // userPic = 'http://img3.duitang.com/uploads/item/201507/23/20150723115018_ma428.thumb.700_0.jpeg';
          if (userPic != "") {
            this.userIcon = userPic;
          }
          this.userName = pagData.USER_NICK_NAME;
    },
    // 环境切换选择
    onSelect(item) {
      this.isShowenvSwitch = false;
      this.envSelectIndex = item.index;
      this.exitLogin(this.envChangedCallBack);
    },
    // 环境切换取消
    onCancel() {
      //  点击取消
    },
    // 环境切换成功方法回调
    envChangedCallBack() {
      let envData = "";
      envData = config.urlMap[this.envSelectIndex];
      store.set("custUrl", envData);
      storage.setStorage("custUrl", envData, 365);
      this.$plugin.usePlugin("Broadcast")(
        'ServiceEnvAdressChange',
        {appUrl: envData.homeUrl}
      );
      Toast("环境切换成功，请重新登录");
    },
    // 版本号cell点击
    versionCellClicked: function() {
      this.versonClickCount++;
      if (this.versonClickCount >= 10) {
        this.versonClickCount = 0;
        Dialog.confirm({
          message: "是否要修改网络环境？"
        })
          .then(() => {
            let currentColor = "#07c160";
            let otherColor = "#007aff";
            let envNames = Object.getOwnPropertyNames(config.urlMap);
            // 获取当前环境key
            let custUrl = store.get("custUrl");
            let envArray = [];
            let envEle = {};
            for (let index = 0; index < envNames.length; index++) {
              envEle = config.urlMap[envNames[index]];
              envArray.push({
                name: envEle.envName,
                color: custUrl.key === envEle.key ? currentColor : otherColor,
                index: envEle.key
              });
            }
            this.envSwitchActions = envArray;
            this.isShowenvSwitch = true;
          })
          .catch(() => {
            // cancel on
          });
      }
    },
    // 加载页面数据
    loadPageData: function() {
      let custNews = store.get("custNews");
      custNews = custNews ? custNews : storage.getStorage("custNews");
      let userData = {
        AREA_CODE: custNews.AREA_CODE,
        USER_CODE: custNews.USER_CODE
      };
      getUserInfoService(userData)
        .then(noteData => {
          this.updatePageData(noteData);
        })
        .catch(error => {
          Toast.fail(error);
        });

      // getMinePageNoticeService(userData)
      // .then(noteData => {
      //     // console.log(noteData);
      // })
      // .catch(error => {
      //     Toast.fail(error);
      // });
    },
    cellClicked: function(idType) {
      switch (idType) {
        case 0:
          // 我的工单
          // this.$router.push({ name: "mineTask" });
          this.$router.push({
            name: "SystemNotice",
            params: { fromHome: 1 }
          });
          break;
        case 1:
          // 账户信息
          this.$router.push({ name: "mineInfoSet" });
          break;
        case 2:
          // 关于
          this.$router.push({ name: "AboutUs" });
          break;
        case 3:
          // app版本号
          this.versionCellClicked();
          break;
        case 4:
          // 退出
          this.isShowExit = true;
          break;
        default:
          break;
      }
    },
    // 退出登录弹框回调
    beforeClose: function(action, done) {
      if (action === "confirm") {
        this.exitLogin();
        done();
      } else {
        done();
      }
    },
    /**
     * 退出登录请求方法
     * callBack  方法回调
     **/
    exitLogin: function(callBack) {
      let custNews = store.get('custNews');
      custNews = custNews ? custNews: storage.getStorage("custNews");
      //移除推送
      deleteAlias(custNews).then((result) => {
        // delete alias success
        log.l("delete alias success " + result);
        cleanTags(custNews);
      }, (err) => {
        log.l('delete alias failed for ' + err);
      });
      //cleanTags(custNews);
      let data = {
        AREA_CODE: custNews.AREA_CODE,
        USER_CODE: custNews.USER_CODE
      };
      getExitAppService(data)
        .then(noteData => {
          if (callBack
          && typeof callBack != 'undefined'
          && callBack != undefined) {
            callBack();
          }
          // 清空缓存
          store.dele('custNews');
          store.dele('userInfo');
          storage.deleteStorage("custNews");
          store.set('currentTabIndex', 0);
          // 移除推送相关
          // 清除小红点
          // 跳转到登录页
          // 通知Home清空缓存数据
          store.get("logoutListener")();
          // delete token info in store and storage
          storage.deleteStorage("accessToken");
          storage.deleteStorage("refreshToken");
          store.dele('accessToken');
          store.dele('refreshToken');
          this.$router.replace({ name: "login" });
        })
        .catch(error => {
          Toast.fail('退出失败，请检查网络');
        });
    },
    getUnreadCount() {
        this.msgTip = false;
        let userInfo = store.get("userInfo") ? store.get("userInfo") : storage.getStorage("custNews");
        getUnreadCount({userCode: userInfo.USER_CODE})
        .then(result => {
            if (result.resultCode == 0) {
              this.msgTip = result.data.unReadMsgCount > 0 ? true : false;
              console.log("mypage get unread msg count success " + this.msgTip);
            } else {
              console.log("mypage get unread msg count failed.");
            }
        })
        .catch(err => {
            console.log("mypage get unread msg count failed for " + err);
        });
    }
  }
};
</script>

