import http from "../http"

/**
 * 获取我的待办工单列表接口
 * @param {*} data
 */
const getWorkingPlanList = (data) =>{
    return new Promise((resolve, reject) => {
        http.get("xwj-property-repair/complQuery/toDoTask/propertyApp", data).then(res => {
            resolve(res)
        }, error => {
            reject(error)
        })
    })
}
