const state = {
    methods: [],  //用户信息
    areaList: [], //管控平台小区列表
    areaCode: '' //上次选中的小区id
}

const mutations = {
    setXtMethod(state, methods) {
        state.methods = methods;
    },
    setAreaList(state, list) {
        state.areaList = list;
    },
    setAreaCode(state, code) {
        state.areaCode = code;
    }
}
// 最后统一导出
export default {
    state,
    mutations
}
