/**
 * get order num
 *
 * @param data
 * @returns {Promise<unknown>}
 */
const getOrderNum = (data) => {
    return new Promise((resolve, reject) => {
        http.get(saleUrl + '/saler/orderNum', {"recAreaCode": data.recAreaCode}, 'baseUrl').then( res => {
            resolve(res);
        }, error => {
            reject(error)
        }).catch(error => {
            reject(error);
        })
    });
};

/**
 * get post code
 *
 * @param data
 * @returns {Promise<unknown>}
 */
const getPostCode = (data) => {
    return new Promise((resolve, reject) => {
        http.post(url + '/app/property/homePage/getUserAreaPostCode.do', {"data": JSON.stringify(data)}, true, "homeUrl").then(res => {
            resolve(res);
        }, error => {
            reject(error);
        }).catch(error => {
            reject(error);
        });
    })
}

