const setAlias = (custNews) => {
    return Promise.resolve().then(() => {
        log.l('set alias with cust news ' + JSON.stringify(custNews) );
        var obj = {
            sequence: Number(custNews.USER_CODE),
            alias: custNews.USER_CODE
        };
        if(!!window.JPush){
            log.i('set ali with seq = ' + obj.sequence + "; ali = " + obj.alias);
            window.JPush.setAlias(obj,
                (result) => {
                    var sequence = result.sequence;
                    var alias = result.alias;
                    log.i("set ali auccess with seq " + sequence + "; ali " + alias);
                }, (error) => {
                    var sequence = error.sequence;
                    var errorCode = error.code;
                    log.e("set ali failed for seq " + sequence + "; err " + errorCode);
                });
        }
    });
}

const deleteAlias = (custNews) => {
    return new Promise((resolve, reject) => {
        var obj = {
            sequence: Number(custNews.USER_CODE),
        };
        if (!!window.JPush) {
            log.i('delete ali with seq = ' + obj.sequence);
            window.JPush.deleteAlias(obj,
                (result) => {
                    var sequence = result.sequence;
                    log.i("Delete ali success " + sequence);
                    resolve(sequence);
                }, (error) => {
                    var sequence = error.sequence;
                    var errorCode = error.code;
                    log.e("Delete ali failed " + sequence + "; err " + errorCode);
                    reject(sequence, errorCode)
                });
        }
    });
}

const setTags = (custNews, tags) => {
    return Promise.resolve().then(() => {
        //设置极光推送 标签
        log.l(JSON.stringify(tags));
        // set tags for JPush
        var obj = {
            sequence: Number(custNews.USER_CODE),
            tags: tags
        };
        if (!!window.JPush) {
            log.i('set tags with JPush.');
            window.JPush.setTags(obj,
                (result) => {
                    log.l("JPushPlugin:setTags is success" + result.sequence);
                }, (error) => {
                    log.l("JPushPlugin:setTags is error" + error.code);
                });
        }
    });
}

const cleanTags = (custNews) => {
    return Promise.resolve().then(() => {
        var obj = {
            sequence: Number(custNews.USER_CODE),
        };
        if (!!window.JPush) {
            window.JPush.cleanTags(obj,
                (result) => {
                    var sequence = result.sequence;
                    log.i("clean tags success " + sequence);
                }, (error) => {
                    var sequence = error.sequence;
                    var errorCode = error.code;
                    log.e("clean tags failed " + sequence + "; err " + errorCode);
                });
        }
    });
}

