const updateCustNews = (obj) => {
    if (undefined === obj) {
        store.dele('custNews');
        return;
    }
    custNews = {//存放小区信息，用户信息
        AREA_CODE: obj.AREA_CODE,//小区代码
        AREA_NAME: obj.AREA_NAME,//小区名称
        CITY_CODE: obj.CITY_CODE,//城市代码
        POST_CODE: obj.POST_CODE,//岗位代码
        EMAIL: obj.EMAIL,//邮箱
        PHONE: obj.PHONE,//电话号码
        PIC_BG_URL: obj.PIC_BG_URL,//朋友圈背景图
        PIC_URL: obj.PIC_URL,//头像
        TEL: obj.TEL,//固定电话
        USER_ACCNT: obj.USER_ACCNT,//账号
        USER_CODE: obj.USER_CODE,//用户账号
        USER_NAME: obj.USER_NAME,//用户名
        USER_NICK_NAME: obj.USER_NICK_NAME,//昵称
        sales_man_flag: obj.sales_man_flag,//社区销售标识 0：不显示销售item 1：显示销售选项
        operation_flag: obj.operation_flag,//社区运营标识 0：显示工单item 1：不显示工单选项
        deptCode: obj.deptCode,//组织代码
    };
    store.set('custNews', custNews);
    store.set('userInfo', custNews);
}

/**
 * This method is abandoned.
 * @param requesUrl
 * @param dataInfo
 * @param sucessCallback
 * @param failedCallback
 */
var postMethod = function (requesUrl, dataInfo, sucessCallback, failedCallback) {
    axios({
        method: 'post',
        url: requesUrl, //url + '/login/loginByPropertyPassword.do',
        data: qs.stringify({"data": JSON.stringify(dataInfo)}),//传参格式 qs.stringify({data: JSON.stringify({key:"value"})})
    })
        .then((response) => {
            if (undefined !== sucessCallback) {
                sucessCallback(response);
            }
        }, (error) => {
            alert("error " + error)
            if (undefined !== failedCallback) {
                failedCallback(error);
            }
        })
        .catch(error => {
            alert("exception " + error)
            if (undefined !== failedCallback) {
                failedCallback(error);
            }
        });
}

/**
 * get current time.
 *
 * @returns {string}
 */
var getNowDate = function () {
    var date = new Date();
    var seperator1 = "-";
    var seperator2 = ":";
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
        month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
    }
    var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
        + " " + date.getHours() + seperator2 + date.getMinutes()
        + seperator2 + date.getSeconds();
    return currentdate;
}

