import axios from "axios";
 import { Dialog } from "vant";
import qs from "qs";
import config from "./common/config";
import { store, storage } from "./utils/store";
import log from './utils/log';

let needExitForAuth = false;
//let isFormHeader = false;
//重试队列，每一项将是一个待执行的函数形式
let retryRequests = [];
//是否正在刷新标记
let isRefreshing = false;
let accessToken = '';

let loading; //定义loading变量
let needLoadingRequestCount = 0;
let accountShowLoading = true;
let http = axios.create({
    baseURL: config.baseUrl,
    withCredentials: false,
    timeout: 30000,//默认15s超时
    headers: {
        "Content-Type": "application/json;charset=utf-8",
        "Authorization": getLocalToken(),
    },
    /*transformRequest: [function (data) {
        if (isFormHeader) {
            return qs.stringify(data);
        } else {
            return JSON.stringify(data);
        }
    }]*/
});
function startLoading() {
    loading = store.get("showLoadingView");
    if (loading) {
        loading(true);
    }
    // loading = Toast.loading({
    //     duration: 0,
    //     mask: false,
    //     message: ''
    // });
}

function endLoading() {
    if (loading) {
        loading(false);
    }

    // loading.clear();
}

function showFullScreenLoading() {
    if (needLoadingRequestCount === 0 && accountShowLoading) {
        startLoading();
    }
    needLoadingRequestCount++;
}

function tryHideFullScreenLoading() {
    if (needLoadingRequestCount <= 0) return;
    needLoadingRequestCount--;
    if (needLoadingRequestCount === 0) {
        endLoading();
    }
}
function logOut(msg) {
    needExitForAuth = true;
    let dialogMsg = msg;
    Dialog.alert({
        message: dialogMsg
    }).then(() => {
        log.i("httpjs: exit app.");
        window.logOut();
        // let method = store.get("goLoginRouter");
        // method();
        // on close
        needExitForAuth = false;
    });
}

function getLocalToken() {
    accessToken = store.get("accessToken", '');
    if (!accessToken) {
        accessToken = storage.getStorage("accessToken", false);
        log.i("httpjs: get local accessToken: " + accessToken);
        store.set('accessToken', accessToken);
        store.set("refreshToken", storage.getStorage("refreshToken", false));
        return accessToken;
    } else {
        return accessToken;
    }
}
http.interceptors.request.use(
    config => {
        showFullScreenLoading();
        /*if (isFormHeader) {
            config.headers["Content-Type"] = "application/x-www-form-urlencoded;charset=utf-8";
        }*/
        if (config.method == "get") {
            delete config.headers["Content-Type"];
        }

        return config;
    },
    error => {
        log.i("httpjs: request interceptor error = " + error);
    }
);

http.interceptors.response.use(
    response => {
        tryHideFullScreenLoading();
        accountShowLoading = true;
        return response;
    },
    error => {
        log.i("httpjs: error.config = " + JSON.stringify(error.config));
        accountShowLoading = true;
        let resultCode = (error.response && error.response.data) ? error.response.data.resultCode : -1;
        if (resultCode == '30130') {
            log.i("httpjs: token 30130 " + error.response.data.msg);
                const config = error.response.config;
                if (!isRefreshing) {
                    isRefreshing = true;
                    // var requestData = {
                    //     refreshToken: storage.getStorage("refreshToken", false)
                    // };
                    let refreshToken = store.get('refreshToken', false) ? store.get('refreshToken', false) : storage.getStorage("refreshToken", false);
                    log.i("httpjs: refreshToken with " + JSON.stringify(refreshToken));
                    return apiAxios("POST", "xwj-sys-auth/oauth/token", { refreshToken }, "baseUrl", false)
                        .then(function(res) {
                            log.i("httpjs: refresh token result = " + JSON.stringify(res));
                            let resStatus = res.resultCode;
                            if (resStatus === '0') {
                                const token = res.data.accessToken
                                // storage.setStorage("accessToken", res.data.accessToken, 0.08);
                                storage.setStorage("accessToken", res.data.accessToken, 0.1);
                                storage.setStorage("refreshToken", res.data.refreshToken, 30);
                                store.set('accessToken', res.data.accessToken);
                                store.set("refreshToken",res.data.refreshToken);
                                log.i("httpjs: refresh token success " + token);
                                config.headers["Authorization"] = token;
                                setTimeout(function() {
                                    // the token refresh will finish quckly, but the request will be multiple
                                    // if the request still not return after token refresh, the failed request
                                    // will not update correctly.
                                    // so, add delay for the retry request list to avoid the condition.
                                    retryRequests.forEach(cb => cb(token));
                                    retryRequests = [];
                                }, 500);
                                return http(config);
                            } else {
                                log.i("httpjs: refresh token failed " + res.resultCode);
                                retryRequests = [];
                                logOut("当前账号登录状态已过期，请重新登录！");
                            }
                            isRefreshing = false;
                        }, (error) => {
                            log.i("httpjs: refresh token failed for - error " + error);
                            retryRequests = [];
                            logOut("当前账号登录状态已过期，请重新登录！");
                        })
                        .catch(function(err) {
                            log.i("httpjs: refresh token error: " + err);
                            isRefreshing = false;
                            retryRequests = [];
                            logOut("当前账号登录状态已过期，请重新登录");
                        })
                        .finally(function() {
                            log.i("httpjs: refresh token finally ");
                            // the condition success/failed has been handled in then and catch.
                            //retryRequests = [];
                            isRefreshing = false;
                        });
                } else{
                    log.i("httpjs: request into retry queue");
                    return new Promise((resolve) => {
                        retryRequests.push((token) => {
                            // const newToken = storage.getStorage("accessToken", false);
                            config.headers["Authorization"] = token;
                            resolve(http(config));
                        });
                    }).catch(function(reason) {
                        log.i('httpjs: request into retry queue exception ' + reason);
                    });
                }
        } else if (resultCode == "30131") {
            log.i("httpjs: error code 30131 multi login " + error.response.data.msg);
            logOut("账号在别的设备上登录," +'\n'+" 当前设备下线");
        } else if (resultCode == '30132' || resultCode == '30135' || resultCode == '30136' || resultCode == '30137') {
            log.i("httpjs: login status expired " + resultCode + ", for reason: " + error.response.data.msg);
            logOut("当前账号登录状态已过期，请重新登录");
        } else if (resultCode == -1) {
            // error without result code, like Network Error or timeout exceeded
            log.i("httpjs: result code exception.");
        } else {
            // error with result code
            log.i("httpjs: result code other condition " + resultCode);
        }
        return Promise.reject(error);
    }
);

function apiAxios(method, url, params, serviceType = "baseUrl", isFormHeader = false) {
    // serviceType = serviceType === null ? baseUrl : serviceType;
    if(serviceType === null){
        serviceType = "baseUrl";
    }
    return new Promise((resolve, reject) => {
        let reqUrl;
        if (/http|https/.test(url)) {
            reqUrl = url;
        } else {
            let { baseUrl, homeUrl } = store.get('custUrl');
            reqUrl = serviceType === 'baseUrl' ? baseUrl + url : homeUrl + url;
        }
        http({
            method: method,
            url: reqUrl,
            //data: method === "POST" || method === "PUT" ? params : null,
            data: method === "POST" || method === "PUT" ? (isFormHeader ? qs.stringify(params):JSON.stringify(params)) : null,
            params: method === "GET" || method === "DELETE" ? params : null,
            headers: {
                "Content-Type": isFormHeader ? "application/x-www-form-urlencoded;charset=utf-8" : "application/json;charset=utf-8",
                "Authorization": getLocalToken(),
            }
        })
            .then(function (res) {
                tryHideFullScreenLoading();
                if (res !== undefined){
                    if (res.status === 200) { //http 响应状态是200为正常 //  && res.data.status 这个不一定有
                        resolve(res.data);
                    } else if (res.status === 200 && !res.data.status) {
                        resolve(res.data);
                    } else {
                        if (!needExitForAuth) {
                            reject(res.message);
                        }
                    }
                }else {
                    reject(res);
                }

            }, (error) => {
                tryHideFullScreenLoading();
                if (!needExitForAuth) {
                    if ("Error: Network Error" == error) {
                        reject("网络异常！");
                    } else if ("Error: timeout of 15000ms exceeded" == error) {
                        reject("请求超时！");
                    } else {
                        reject(error);
                    }
                }
            })
            .catch(function (err) {
                tryHideFullScreenLoading();
                // 服务连接失败err.message为"Network Error", 连接超时（就是超过设定的timeout的时间）err.message为timeout of 3000ms exceeded
                if (!needExitForAuth) {
                    if ("Network Error" == err.message) {
                        reject("网络异常！");
                    } else if ("timeout of 15000ms exceeded" == err.message) {
                        reject("请求超时！");
                    } else {
                        reject(err.message || err);
                    }
                }
            });
    });
}

export default {
    get: function (url, params, servceType,accountLoading = true) {
        //isFormHeader = true;
        accountShowLoading = accountLoading;
        return apiAxios("GET", url, params, servceType, false);
    },
    post: function (url, params, isFormData, servceType,accountLoading = true) {
        //isFormHeader = isFormData;
        accountShowLoading = accountLoading;
        return apiAxios("POST", url, params, servceType, isFormData);
    },
    put: function (url, params, servceType) {
        return apiAxios("PUT", url, params, servceType, false);
    },
    delete: function (url, params, servceType) {
        return apiAxios("DELETE", url, params, servceType, false);
    }
};
