import http from "xt/http";
import baseUrl from '../config'

//获取三通小区功能
const getSystemDicChildren = (parentId) => (http.get(baseUrl  + '/web/system/dic/getSystemDicChildrenByParentId', { parentId }));

//获取三通小区列表
const getSystemDicByParent = () => (http.get(baseUrl  + '/web/system/dic/getSystemDicByParentIdContent', { parentId: 'PH', pageSize: '100' }));

// 获取访客角色
const getFaceRole = (data) => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/face/app/parking/getFaceRoleTime', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 获取剩余车位数量
const getParkingInfo = (data) => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/app/parking/getinfo', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 获取绿色通道时间
const getGreenTime = (data) => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/face/app/parking/getGreen', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 选择访问地址
const getDoorNumInfo = (data) => {
	return new Promise((resolve, reject) => {
		http.post('/subsystem/app/parking/getDoorNumInfo', data, true)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 上传人脸信息
const addFaceInfo = (data) => {
	return new Promise((resolve, reject) => {
		http.post('/web/face/add/faceAndRoom', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 下发设备
const face2Device = (id, regionId) => {
	return new Promise((resolve, reject) => {
		http.post('/subsystem/face2Device/app/toHikDevice?faceId=' + id + '&regionId=' + regionId)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 提交预约
const bookingInfo = (data) => {
	return new Promise((resolve, reject) => {
		// http.post(baseURL + '/subsystem/app/parking/booking', data, true)
		http.post('/subsystem/face/app/parking/booking', data, true)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 获取二维码信息
const getQRInfo = (data) => {
	return new Promise((resolve, reject) => {
		http.post('/subsystem/door/control/QRCode', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 预约记录
const bookingList = (data) => {
	return new Promise((resolve, reject) => {
		// http.get(baseURL + '/subsystem/app/parking/getparkingNew', data)
		http.get('/subsystem/face/app/parking/getParkingNew', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 取消预约
const unBooking = (data) => {
	return new Promise((resolve, reject) => {
		// http.get(baseURL + '/subsystem/app/parking/unbooking', data)
		http.get('/subsystem/face/app/parking/unBooking', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 删除预约
const deleteBooking = (data) => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/face/app/parking/delete', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 获取临时停车列表
const getParkingList = (data) => {
	return new Promise((resolve, reject) => {
		http.post('/subsystem/app/parking/getparking', { data: JSON.stringify(data) }, true)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

// 获取停车列表刷新时间
const getRefreshTime = () => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/face/app/parking/getTime')
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

export {
	getFaceRole,
	getParkingInfo,
	getGreenTime,
	getDoorNumInfo,
	addFaceInfo,
	face2Device,
	bookingInfo,
	getQRInfo,
	bookingList,
	unBooking,
	deleteBooking,
	getParkingList,
	getRefreshTime,
	getSystemDicChildren,
	getSystemDicByParent,
	baseUrl
}