<template>
    <div class="apptitle" >
        <div class="back_div"  v-show="containerType !== 'Homepage'" @click="goBack">
            <img class="back_icon" :src="exitImg">
        </div>
        <div v-show="containerType !== 'Homepage'">{{containerTitle}}</div>

        <van-dropdown-menu v-show="containerType === 'Homepage' && optionlist.length > 0" class="app-title-div" :overlay="true">
            <van-dropdown-item v-model="selectValue" :options="optionlist" @change="onSelectionChanged" />
        </van-dropdown-menu>
    </div>
</template>

<script>
    export default {
        name: 'AppTitle',
        data() {
            return {
                exitImg: require('../../assets/imgs/title/back-white.png'),
                selectValue: 0,
                optionlist: [
                    /*{ text: '小区1', value: 0 },
                    { text: '小区2', value: 1 },
                    { text: '小区3', value: 2 }*/
                ],
            }
        },
        props: {
            containerType: String,
            containerTitle: String
        },
        methods: {
            syncSelection: function () {
                this.$emit('syncSelection', this.selectValue);
            },
            onSelectionChanged: function () {
                this.syncSelection();
            },
            goBack: function () {
                this.$router.back();
            },
            syncVillList: function (val) {
                if (val && val.length > 0) {
                    this.optionlist = [];
                    for (var i = 0; i < val.length; i++) {
                        this.optionlist.push({text: val[i].AREA_NAME, value: i});
                    }
                }
            },
            updateSelection: function (val) {
                if (val) {
                    this.selectValue = val;
                }
            }
        }
    }
</script>

