import {store} from "../utils/store";

export default {
    camera: {
        origin(success, fail) {
            navigator.camera.getPicture(function (imageURI) {
                success(imageURI);
            }, function (err) {
                if ('No Image Selected' != err) {
                    //fail(err);
                }
            }, {
                quality: 70,
                targetWidth: 600,
                targetHeight: 600,
                correctOrientation: true,
                encodingType: Camera.EncodingType.JPEG,
                destinationType: Camera.DestinationType.FILE_URI
            })
        },
        browser(success) {
            //browser todo
            success('http://219.147.31.51:80/hisense/download/sys/e4f309ce3bc1416085c24c4420c30cad.jpg');
        }
    },
    getPictures: {
        origin(maximumImagesCount,success, fail) {
            window.imagePicker.getPictures(
                (images) => {
                    success(images);
                }, (error) => {
                    //fail("选择本地图片失败");
                }, {
                width: 600,
                height: 600,
                encodingType: Camera.EncodingType.JPEG,
                maximumImagesCount, //每次可选择图片数目
                quality: 70,  //图片质量
                outputType: 0,
                title: '选择图片',
            }
            );

        }, browser(maximumImagesCount,success) {
            //browser todo
            success(['http://219.147.31.51:80/hisense/download/sys/e4f309ce3bc1416085c24c4420c30cad.jpg', 'http://219.147.31.51:80/hisense/download/sys/368f302d031c4a2488bdd72a75a337fe.jpg']);
        }
    },
    fileTransfer: {
        origin(reqUrl, fileURL, success, fail, options) {
            let ft = new FileTransfer();
            /*var options = {
                maximumImagesCount: 1,
                fileKey: "file",//相当于form表单项的name属性
                fileName: fileURL.substr(fileURL.lastIndexOf('/') + 1),
                mimeType: "image/jpeg"
            };*/
            ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
        }, browser(reqUrl, fileURL, success, fail) {
            //browser todo
            success({
                response: JSON.stringify({
                    returnCode: "success",
                    headUrl:[{"MSG":"上传成功","WJDM":"379e64b3fa1145c0991ff3ad57442e43","URL":"http://219.147.31.38:8145/hisense/download/sys/cc1c3254251d44418f8db248e4501ec0.jpg"}]
                })
            });
        }
    },
    jPush: {
        origin(success, failed) {
            // 初始化
            if(!!window.JPush){
                window.JPush.init();
                console.log('init完成1');
                window.JPush.setDebugMode(true);
                window.JPush.resetBadge();
                window.JPush.setApplicationIconBadgeNumber(0);
            }else{
                window.plugins.jPushPlugin.init();
                console.log('init完成2');
                window.plugins.jPushPlugin.setDebugMode(true);
                // 清除角标
                window.plugins.jPushPlugin.resetBadge();
                window.plugins.jPushPlugin.setApplicationIconBadgeNumber(0);
            }

            document.addEventListener("jpush.receiveRegistrationId", function (event) {
                console.log("receiveRegistrationId" + JSON.stringify(event));
            }, false)
            // 设置回调
            document.addEventListener("jpush.openNotification", function(event) {
                console.log("openNotification" + JSON.stringify(event));
                // 跳转到工单详情/延期审批详情（内部/业主）
                store.get("listenToNotificationOpen")(event);
                // 跳转到发现界面处理逻辑（noteCode不为空的情况）
                store.get("openNotificationForFind")(event);
            }, false);
            document.addEventListener("jpush.receiveNotification", function(event) {
                console.log("receiveNotification" + JSON.stringify(event));
            }, false);
            success();
        },
        browser(success) {
            success();
        }
    },
    /***
     * name：string类型  广播/通知名字
     * objectBody：对象 包含属性和值
     * ***/
    Broadcast: {
        origin(name, objectBody) {
            window.broadcaster.fireNativeEvent( name, objectBody, function() {
                console.log( "js端：发起广播/通知" );
            });
        },
        browser(name, objectBody) {
            console.log( "当前环境不支持插件broadcaster 广播通知name: " + name );
        }

    }
}

