// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  if (secondRouterMap.includes(to.name)) {
    store.set('currentRouterName',to.name);
    Eventbus.$emit('secondRouter', true);
  } else {
    Eventbus.$emit('secondRouter', false);
  }
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});

export {
  router
};
