import http from "../http";

/**
 * 获取我的待办工单列表接口
 * @param {*} data
 */
const getInterWorkingPlanList = (data) =>{
    return new Promise((resolve, reject) => {
        http.get("xwj-property-repair/internalComplQuery/toDoTask/propertyApp", data).then(res => {
            resolve(res)
        }, error => {
            reject(error)
        })
    })
}
