<template>
  <div style="height:100%;display:flex;flex-direction:column;">
    <van-nav-bar title="发现" @click-right="addNote">
      <slot slot="right" name="right">
        <img class="nav-bar-custom-icon" src="../../assets/imgs/find/camera.png" alt />
      </slot>
    </van-nav-bar>
    <div id="refreshWrapper">
      <van-pull-refresh v-model="isLoading" @refresh="refresh">
        <van-list
          v-model="loading"
          :finished="finished"
          @load="loadMoreNote"
          :offset="700"
          :finished-text="noteData.length > 0 ? '无更多数据...' : ''"
        >
          <!--header banner-->
          <div class="banner" :style="background">
            <div class="user-msg">
              <p>{{userInfo.USER_NICK_NAME}}</p>
              <img v-if="userInfo.PIC_URL" class="user-avatar" :src="userInfo.PIC_URL" alt @click="goMyComments"/>
              <img v-else class="user-avatar" src="../../assets/imgs/find/avatar.png" alt @click="goMyComments"/>
            </div>
          </div>
          <!--content-->
          <div class="no-list" v-show="!noteData.length">
            <div>暂无消息</div>
          </div>
          <!-- 加载的内容-->
          <div class="new-msg" v-if="newMsg">
            <div class="msg">
              <div @click="goNewMsg">您有{{newMsg}}条新消息</div>
              <van-icon name="arrow" color="#ffffff" size="10px"/>
            </div>
          </div>

          <section class="friend-message" v-for="(item,m) in noteData" v-bind:key="m">
            <div class="friend-msg" v-if="item != null">
              <div>
                <img v-if="item.headPic" :src="item.headPic" alt />
                <img v-else src="../../assets/imgs/find/avatar.png" alt />
                <p>{{item.nickName}}</p>
              </div>
              <div class="func-handle">
                <img
                  v-if="item.noteType == 1"
                  src="../../assets/imgs/find/property.png"
                  alt
                  width="40px;"
                />
                <span
                  v-bind:class="{'checked-class':item.cateName}"
                  @click="showSelectionDialog(item.noteCode)"
                >{{item.cateName || '分类'}}</span>
                <div class="cls-more-div" @click="reportMenu(item)">
                  <img class="cls-icon-more" src="../../assets/imgs/find/find_report.png" alt="">
                </div>
              </div>
            </div>
            <div class="message-container" v-if="item != null">
              <p>{{item.content}}</p>
              <van-row
                class="message-img"
                v-bind:class="{'big-image': item.notePic && item.notePic.length == 1}"
                v-if="item.notePic"
              >
                <van-col span="6" v-for="(imgurl,index) in item.notePic" v-bind:key="index">
                  <img @click="preview(item.notePic,index)" :src="imgurl" alt />
                </van-col>
              </van-row>
              <!-- <van-row class="message-img" v-else>
                <van-col span="20" v-for="(imgurl,n) in item.SRC" v-bind:key="n">
                  <img :src="imgurl.src" alt />
                </van-col>
              </van-row>-->
            </div>
            <div class="handler-container" v-if="item != null">
              <van-row span="8" type="flex" justify="space-between">
                <van-col class="desc">
                  <span>{{item.createdTime}}</span>
                  <span @click="deleteNote(item.noteCode)">删除</span>
                </van-col>
                <van-col span="8" class="handle-type">
                  <span @click="setLike(item.noteCode,m,item.likerList)">
                    <img class="cls-icon" src="../../assets/imgs/find/unlove_icon.png" alt="" v-if="checkLike(item.likerList)">
                    <img class="cls-icon" src="../../assets/imgs/find/love_icon.png" alt="" v-else>
                    <span v-if="item.likerList">{{item.likerList.length}}</span>
                  </span>
                  <span @click="addComments(item.noteCode,m)">
                    <img class="cls-icon" src="../../assets/imgs/find/com_icon.png" alt="">
                    {{item.commentList?item.commentList.length:''}}
                  </span>
                </van-col>
              </van-row>
            </div>
            <div class="reply-container" v-if="item != null">
              <van-row type="flex" class="goods-group" v-if="item.likerList.length > 0">
                <span class="class-span">
                  <img class="cls-icon" src="../../assets/imgs/find/praise_icon.png" alt="">
                </span>
                <van-col class="goods-person">
                  <span v-for="(ele,a) in item.likerList" v-bind:key="a">
                    {{ele.nickName}}
                    <i v-if="a != item.likerList.length-1">,</i>
                  </span>
                </van-col>
              </van-row>
              <div v-if="item.commentList">
                <div type="flex" class="reply-group" v-for="(eva,d) in item.commentList" v-bind:key="d">
                  <div class="reply-item">
                    <div class="reply-desc" @click="replyAction(eva,item.noteCode,m)">
                      <span
                        class="reply-name"
                        v-bind:class="{'reply-flag':!eva.returner}"
                      >
                        {{eva.nickName}}
                        <img
                          v-if="eva.isProperty == 1"
                          src="../../assets/imgs/find/property.png"
                          width="36px;"
                        />
                      </span>
                      <span class="result" v-if="eva.returner">回复</span>
                      <span
                        class="reply-name"
                        v-bind:class="{'reply-flag':eva.returner}"
                      >{{eva.returner}}</span>
                      <p class="reply-text">{{eva.content}}</p>
                    </div>
                    <van-row>
                      <van-col span="6" v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                        <img @click="preview(eva.evalPic,c)" :src="ind" class="small-reply-img" alt />
                      </van-col>
                    </van-row>
                  </div>
                </div>
              </div>
              <van-row></van-row>
            </div>
          </section>
        </van-list>
      </van-pull-refresh>
    </div>
    <!-- <van-popup v-model="show" class="report-menu" position="bottom" :style="{bottom:'64px'}">
        <div @click="report">举报</div>
        <div @click="reportMenu">取消</div>
      </van-popup>-->
      <van-action-sheet
        v-model="reportShow"
        :actions="reportActions"
        cancel-text="取消"
        round
        close-on-click-action
        @select="reportAction"
      />
      <van-action-sheet
        v-model="deleteShow"
        :actions="deleteActions"
        cancel-text="取消"
        round
        close-on-click-action
        @select="delCommentAction"
      />
      <van-dialog v-model="showSelect" show-cancel-button @confirm="selectionConfirm">
        <div style="line-height:50px;text-align:center;">类别选择</div>
        <div style="height:auto">
          <van-picker ref="selectedPicker" :columns="selections" :item-height="28" value-key="noteCatName" />
        </div>
      </van-dialog>
      <Preview :images="images" ref="myPreview" @indexChange="indexChange"></Preview>
  </div>
</template>

<script>
import {
  getAllNoteService,
  saveLikeService,
  getOneNoteMsgService,
  getSelectionService,
  updateNoteCatService,
  delNoteDataService,
  delCommentInforService,
  getNewMsgService,
} from "../../services/newFindService";
import {
  updateCustNews
} from "../../services/loginService"
import {
  getUserInfoService,
} from "../../services/mineService";
import { ImagePreview, Toast, Dialog } from "vant";
import Preview from "@/components/preview/Preview.vue";
import { store, storage } from "../../utils/store";
import log from "../../utils/log"
import typeJudge from "../../utils/typeJudge";
import rightIcon from "@/assets/imgs/home/left.png";
// import { eventBus } from "../../utils/eventBus";
export default {
  name: "Findpage",
  created() {
    this.monitorVisibileState();
  },
  mounted() {
    this.getSelectionList();
    store.set('updateFindUserInfo', (userData) => {
      // 更新userinfo，更新头像
      let userPic = userData.PIC_URL;
      this.userInfo.PIC_URL = userData.PIC_URL;
      if (userPic != "") {
        this.userIcon = userPic;
      }
      this.userName = userData.USER_NICK_NAME;
      this.userInfo.USER_NICK_NAME = userData.USER_NICK_NAME;
      // log.i("FindPage-1 " + JSON.stringify(this.userInfo));
      updateCustNews(this.userInfo);
      storage.setStorage("custNews", this.userInfo, 60);
    });
    store.set("listenToAreCodeChange", () => {
      // area code changed, update find list
      log.i("area code changed, update find data.");
      this.refresh();
    });
    store.set("updateNewMsgForFind", () => {
      log.i("update when go find from new msg list or jpush msg.");
      this.refresh();
    })
  },
  props: {
    activeIndex: {
      type: Number
    }
  },
  data() {
    return {
      count: 0,
      selections: [],
      deleteActions: [{ name: "删除" }],
      reportActions: [{ name: "举报" }],
      selectCatCode: "",
      showSelect: false,
      isFirst: true,
      reportShow: false,
      deleteShow: false,
      loading: false,
      finished: false,
      isLoading: false,
      noteTotal: 0,
      pageNumber: 1,
      background: {
        backgroundImage:
          "url(" + require("../../assets/imgs/find/banner.png") + ") ",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      },
      noteData: [],
      userInfo: {},
      rightIcon: rightIcon,
      newMsg: 0,
      isLikeSetting: false,
      images: [],
      imgComps: [],
      noteLoadTime: 0,
      noteLoadFinished: true,
    };
  },
  watch: {
    activeIndex(index) {
      if (index === 1 && this.isFirst) {
        this.loadFindUserData();
        this.loadNotes(1);
        this.getNewMsg();
        this.isFirst = false;
      }
    },
    deleteShow(showing) {
      store.get("hideTabBar")(showing);
    },
    reportShow(showing) {
      store.get("hideTabBar")(showing);
    }
  },
  components: {
    Preview: Preview,
  },
  methods: {
    /**
     * 刷新数据
     **/
    refresh() {
        this.loadNotes(1);
        this.getNewMsg();
        this.loadFindUserData();
    },
    getNowTime() {
      var now = Date.now();
      console.log("get now time " + (now - this.noteLoadTime));
      if (now - this.noteLoadTime <= 150) {
        this.noteLoadTime = now;
        // ignore
        return false;
      } else {
        this.noteLoadTime = now;
        return true;
      }
    },
    /**
     * 加载朋友圈信息
     * @param
     * pageNumber  Number      当前页码
     * isRefresh   Boolean     本次调用是否为从头刷新
     **/
    loadNotes(pageNumber, isRefresh = true) {
      console.log("note load finished " + this.noteLoadFinished + ";page number " + this.pageNumber)
      if (!this.noteLoadFinished) {
        log.i("Load notes not finished, ignore!!!");
        return;
      }
      if (!this.getNowTime()) {
        log.i("Load notes twice quickly less than 150ms, ignore!!!");
        return;
      }
      this.noteLoadFinished = false;
      let custNews = (store.get("custNews") && store.get("custNews").AREA_CODE) ?
              store.get("custNews") : storage.getStorage("custNews");
      if (this.userInfo.AREA_CODE == "" || this.userInfo.AREA_CODE == undefined) {
        log.i("area code empty, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
      }
      if (this.userInfo.AREA_CODE != custNews.AREA_CODE) {
        log.i("area code changed, reset first.");
        this.userInfo = (custNews && custNews.AREA_CODE) ? custNews : storage.getStorage("custNews");
      }
      if (pageNumber == 1) {
        this.finished = false;
      }
      getAllNoteService({
        areaCode: this.userInfo.AREA_CODE,
        page: pageNumber,
        rows: 10
      })
        .then(noteData => {
          if (isRefresh) {
            this.noteData = [];
            this.pageNumber = 1;
          }
          if (!noteData.data.hasNextPage) {
            this.finished = true;
          }
          this.loading = false;
          this.isLoading = false;
          noteData.data.list.forEach(element => {
            this.noteData.push(element);
          });
          this.noteLoadFinished = true;
          this.pageNumber++;
        }, err => {
          this.loading = false;
          this.isLoading = false;
          this.finished = true;
        })
        .catch(error => {
          this.finished = true;
          this.loading = false;
          this.isLoading = false;
          Toast.fail(error);
        });
    },
    loadFindUserData: function() {
      let custNews = store.get("custNews");
      let userData = {
        AREA_CODE: custNews.AREA_CODE,
        USER_CODE: custNews.USER_CODE
      };
      getUserInfoService(userData)
        .then(noteData => {
          setTimeout(()=> {
            let userPic = noteData.PIC_URL;
            if (userPic != "") {
              custNews.PIC_URL = userPic;
            }
            custNews = store.get("custNews");
            custNews = custNews ? custNews : storage.getStorage("custNews");
            custNews.USER_NICK_NAME = noteData.USER_NICK_NAME;
            // log.i("FindPage-2 " + JSON.stringify(custNews));
            this.userInfo = custNews;
            updateCustNews(custNews);
            storage.setStorage("custNews", custNews, 60);
            store.get("updatePageData")(custNews);
          }, 500);
        })
        .catch(error => {
          Toast.fail(error);
        });
    },
  monitorVisibileState: function () {
    document.addEventListener("visibilitychange", () => {
      if (document.visibilityState == "visible") {
        // visible update pic
        this.userInfo = store.get("userInfo") ? store.get("userInfo") : storage.getStorage("custNews");
      } else if (document.visibilityState == "hidden") {
        // do not care
      }
    });
  },
    /**
     * 获取未读的消息
     **/
    getNewMsg() {
      getNewMsgService({
          ownerCode: this.userInfo.USER_CODE,
          areaCode: this.userInfo.AREA_CODE
      }).then(
        result => {
            this.newMsg = result.data;
        },
        err => {
            log.e("the new message return failed " + err)
        }
      )
      .catch(error => {
        this.newMsg = 0;
      });
    },

    /**
     * 跳转到新消息列表
     **/
    goNewMsg() {
      this.$router.push({
        name: "newMsg",
        params: { fromFind: 1 }
      });
    },

    /**
     * 跳转到我的相册页面
     **/
    goMyComments() {
      this.asyncUpdateNoteMsg();
      this.$router.push({
        name: "myComments",
        params: { fromFind: 1 }
      });
    },

    /**
     * 回复好友评论
     * @param
     * ownerCode   String      当前选择的评论用户编码
     **/
    replyAction(evaMap, noteMap, noteIndex) {
      if (this.userInfo.USER_CODE === evaMap.userCode) {
        this.deleteShow = true;
        this.replyNoteIndex = noteIndex;
        this.delActionObj = {
          noteCode: noteMap,
          evalCode: evaMap.evalCode
        };
      } else {
        let replys = Object.assign(typeJudge.objectFullCopy(evaMap), {
          NOTE_CODE: noteMap
        });
        this.asyncUpdateNoteMsg(noteMap, noteIndex);
        this.$router.push({
          name: "addNote",
          params: { pageType: "reply", replyParam: JSON.stringify(replys) }
        });
      }
    },

    /**
     * 通过store异步更新单条note信息
     * @param
     * nodeCode   String      当前选中note编码
     * noteIndex  Number      当前操作的信息的索引
     **/
    asyncUpdateNoteMsg(noteCode, noteIndex) {
      store.set("updateNoteMsg", (onlyOne = true) => {
        if (onlyOne) {
          //仅更新单条
          getOneNoteMsgService({"noteCode": noteCode})
            .then(result => {
              this.$set(this.noteData, noteIndex, result.data);
            })
            .catch(error => {
              Toast.fail(error);
            });
        } else {
          //更新多条
          this.loadNotes(1);
        }
      });
    },

    addNote() {
      this.asyncUpdateNoteMsg();
      this.$router.push({ name: "addNote", params: { pageType: "publish" } });
    },

    /**
     * 删除评论操作
     * @param
     **/
    delCommentAction() {
      let { noteCode, evalCode } = this.delActionObj;
      delCommentInforService(noteCode, evalCode)
        .then(() => {
          return getOneNoteMsgService({"noteCode": noteCode});
        })
        .then(result => {
          this.$set(this.noteData, this.replyNoteIndex, result.data);
        })
        .catch(error => {
          Toast.fail(error);
        });
    },

    /**
     * 举报操作
     * @param
     **/
    reportAction() {
      this.$router.push({
        name: "addNote",
        params: {
          pageType: "report",
          reportParam: JSON.stringify({
            BIZ_CODE: this.checkedReport.noteCode,
            OWNER_CODE: this.checkedReport.userCode,
            TYPE_FLAG: this.checkedReport.noteType
          })
        }
      });
    },

    /**
     * 增加评论
     * @param
     * noteCode   String   当前选中note编码
     * noteIndex  Number   当前操作的信息的索引
     **/
    addComments(noteCode, noteIndex) {
      this.asyncUpdateNoteMsg(noteCode, noteIndex);
      this.$router.push({
        name: "addNote",
        params: { pageType: "comments", noteCode }
      });
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      let urls = src.map(val => val);
      // ImagePreview({ images: urls, startPosition: index });
      this.images = urls;
      this.indexChange(index);
      this.$refs.myPreview.setIndex(index);
      this.$refs.myPreview.setIsShowPreview(true);
      store.get("hideTabBar")(true);
    },

    /**
    * 图片切换
    **/
    indexChange(index) {
      var src = this.images[index];
      if(src.lastIndexOf("@") == -1) {
        return;
      }
      var index1 = src.lastIndexOf(".");
      var index2 = src.lastIndexOf("@");
      var newSrc = src.substring(0, index2) + src.substring(index1, src.length);
      var img;
      if (this.imgComps.length == 0 || !this.imgComps[index]) {
        this.imgComps[index] = new Image();
      } else {
        this.imgComps[index].onload = null;
      }
      img = this.imgComps[index];
      // var img = new Image();
      img.onload = () => {
        this.$set(this.images, index, newSrc);
      }
      img.src = newSrc;
    },

    /**
     * 点赞行为(增加与取消)
     * @param
     * nodeCode  String   当前点选节点code
     * noteIndex Number   当前点选节点索引
     * likeMap   Array<Object> 当前节点所有点赞集合
     **/

    setLike(noteCode, noteIndex, likeMap) {
      if (this.isLikeSetting) {
        console.log("like is setting.");
        return;
      }
      this.isLikeSetting = true;
      let reqData = {
        "concemflag": 1,
        "isProperty": 1,
        "noteCode": noteCode,
        "userCode": this.userInfo.USER_CODE
      };

      if (likeMap) {
        reqData.concemflag = likeMap.some(val => val.userCode == reqData.userCode)
                ? 0
                : 1;
      } else {
        reqData.concemflag = 1;
      }
      saveLikeService(reqData)
        .then(res => {
          return getOneNoteMsgService({"noteCode": noteCode});
        })
        .then(result => {
          this.isLikeSetting = false;
          this.$set(this.noteData, noteIndex, result.data);
        })
        .catch(error => {
          this.isLikeSetting = false;
          Toast.fail(error);
        });
    },

    /**
     * 删除当前所选的note
     * @param
     *  noteCode  String  当前所选的noteCode
     **/
    deleteNote(noteCode) {
      Dialog.confirm({
        title: "提示",
        message: "确定删除此贴？"
      }).then(() => {
        return delNoteDataService(noteCode, this.userInfo.USER_CODE)
          .then(result => {
            this.loadNotes(1);
          })
          .catch(error => {
            Toast.fail(error);
          });
      });
    },

    /**
     * 上拉加载更多数据
     * @param
     **/
    loadMoreNote() {
      console.log("load more");
      this.loadNotes(this.pageNumber, false);
    },

    /**
     * 校验当前的点赞列表中是否包含当前登录用户，以此来更改点赞图标
     * @param
     *  likeMap  Array<Object>  当前的点赞集合数据
     * @return
     *  Boolean  当前登录账户是否已经点赞过
     **/
    checkLike(likeMap) {
      if (!likeMap) {
        return false;
      }
      return likeMap.some(val => val.userCode == this.userInfo.USER_CODE);
    },

    /**
     * 获取全部的分类列表
     * @param
     *
     **/
    getSelectionList() {
      getSelectionService()
        .then(result => {
          this.selections = result.data;
        })
        .catch(error => {
          Toast.fail(error);
        });
    },

    /**
     * 点击吊起举报菜单时保存当前的选中的note对象
     * @param
     * noteMap  Object  当前点击的note对象
     **/
    reportMenu(noteMap) {
      this.reportShow = !this.reportShow;
      this.checkedReport = noteMap;
    },

    /**
     * 设置当前note信息分类（吊起选择分类弹窗）
     * @param
     * catCode   Number  当前选择的catCode编码
     **/
    showSelectionDialog(catCode) {
      this.showSelect = true;
      this.selectCatCode = catCode;
    },

    /**
     * selection 弹窗点击确定回调
     * @param
     **/
    selectionConfirm() {
      let [ CODE ] = this.$refs.selectedPicker.getValues();
      let reqData = {"noteCateCode": CODE.noteCatCode , "noteCode": this.selectCatCode, "userCode": this.userInfo.USER_CODE};
      updateNoteCatService(reqData)
        .then(() => {
          //更新成功后刷新页面
          this.loadNotes(1);
        })
        .catch(error => {
          Toast.fail(error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped lang="scss">
$bigSize: 18px;
$smallSize: 14px;
$cSize: 16px;
$superSmall: 12px;
$replyColor: #5e9be3;
.van-nav-bar {
  position: relative !important;
}
#refreshWrapper {
  flex: 1;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 66px;
  .no-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 14px;
    padding-top: 50px;
    img {
      width: 60px;
      margin-bottom: 15px;
    }
  }
}
.banner {
  width: 100%;
  height: 85px;
  position: relative;
  margin-bottom: 20px;
  .user-msg {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    height: 58px;
    bottom: -15px;
    font-size: $bigSize;
    color: #fff;
    padding-right: 20px;
    > img {
