const DebugLevel = {
    l: 0,
    i : 1,
    w : 2,
    e : 3,
};
function Log() {
    const debug = true;
    const debugLevel = DebugLevel.i;// to control log level: log-0, info-1, warn-2 erro-3
    this.init = () => {
    };
    this.init.call(this);

    /**
     * show msg log if debug
     *
     * @param msg
     */
    var log= (level, msg) => {
        try {
            if (debug && level >= debugLevel) {
                switch (debugLevel) {
                    case DebugLevel.l:
                        console.log(msg);
                        break;
                    case DebugLevel.i:
                        console.info(msg);
                        break;
                    case DebugLevel.w:
                        console.warn(msg);
                        break;
                    case DebugLevel.e:
                        console.error(msg);
                        break;
                }
            }
        } catch (e) {
            console.log(JSON.stringify(e));
        }
    };

    var dlog = (msg) => {
        if (debug) {
            console.log(msg);
        }
    }

    return {
        log,
        dlog
    }

}
var log = new Log();
var d = (msg) => {return log.dlog(msg);}
var l = (msg) => {return log.log(DebugLevel.l, msg);}
var i = (msg) => {return log.log(DebugLevel.i, msg);}
var w = (msg) => {return log.log(DebugLevel.w, msg);}
var e = (msg) => {return log.log(DebugLevel.e, msg);}

export default {
    d, l, i, w, e
}
