import http from "../http"

/**
 * 获取延期审批列表
 * @param {*} data
 */
const getDelayList = (data) =>{
    return new Promise((resolve, reject) => {
        http.get("xwj-property-repair/workOrderDelay/getMyDelayListForProperty", data).then(res => {
            resolve(res)
        }, error => {
            reject(error)
        })
    })
}

/**
 * 获取延期审批详情
 * @param {*} data
 */
const getDelayDetail = (data) =>{
    return new Promise((resolve, reject) => {
        http.get("xwj-property-repair/workOrderDelay/getMyDelayInfoByUuid", data).then(res => {
            resolve(res)
        }, error => {
            reject(error)
        })
    })
}


/**
 * 审批延期申请
 * @param {*} data
 */
const checkWorkDelay = (data) =>{
    return new Promise((resolve, reject) => {
        http.post("xwj-property-repair/workOrderDelay/approveWorkOrderDelay", data).then(res => {
            resolve(res)
        }, error => {
            reject(error)
        })
    })
}

export {
    getDelayList,
    getDelayDetail,
    checkWorkDelay
};

