import pluginMap from "./plugin.map";
import config from "../common/config";
import log from '../utils/log';
function Adapter(deviceReadyCallback) {
    var platformAdapter = () => {
        let isOriginPlatform = config.isOriginPlatform;
        log.i('The platform is original ' + isOriginPlatform);
        let pluginInstance = new Plugins(isOriginPlatform);
        let deviceComplete = false;
        if (isOriginPlatform) {
            document.addEventListener('deviceready', () => {
                deviceComplete = true;
                deviceReadyCallback(pluginInstance);
            }, false);
        }
        else {
            deviceComplete = true;
            deviceReadyCallback(pluginInstance);
        }
        setTimeout(() => {
            if (!deviceComplete) {
                console.warn("device=>>>设备准备超时");
                deviceReadyCallback(pluginInstance);
            }
        }, 20000);
    }

    this.init = () => {
        platformAdapter();
    }

    this.init.call(this);
}

function Plugins(isOriginalDevice) {
    this.usePlugin = (pluginName) => {
        if (isOriginalDevice) {
            return pluginMap[pluginName].origin
        } else {
            return pluginMap[pluginName].browser;
        }
    }
}




export default Adapter;
