<template>
  <div>
    <van-image-preview v-model="isShowPreview" :images="images" @close="close" @change="setIndex" :startPosition="activeIndex">
      <div slot="index" class="dot-box" v-show="images.length > 1">
        <div v-for="(item, ind) in images" :key="ind" class="dot" :class="{'active-dot':ind == activeIndex}"></div>
      </div>
    </van-image-preview>
  </div>
</template>

<script>
import { store } from "../../utils/store";
export default {
  name: "Preview",
  props: {
    images: {
        type: Array,
        default: () => []
    },
  },
  data() {
    return {
        activeIndex: 0,
        isShowPreview: false
    };
  },
  methods: {

    /**
     * 预览的图片变化时,index的变化
     */
    setIndex(index) {
      this.activeIndex = index;
      this.$emit("indexChange", index);
    },

    /**
     * 改变图片展示或隐藏
     */
    setIsShowPreview(status = false) {
      this.isShowPreview = status;
    },

    /**
     * 关闭预览
     */
    close() {
      store.get("hideTabBar")(false);
    }
  }
};
</script>

